import React from "react";
import "../app/cards.css";

export function Card(props: any) {
  return (
    <div className="card">
      <div className="card-img">{props.img}</div>
      <image></image>
      <div>
        <h3 className="card-titulo">{props.titulo}</h3>
        <p className="card-descricao">{props.descricao}</p>
      </div>
    </div>
  );
}